@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
}
body{
  background-color: #f5f6fa;
}
.app{
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}
.title{
  /*background-color: lavenderblush;*/
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 40px;
  padding: 5px;

}
.app__header{
  display: flex;
  align-items: center;
  padding: 5px;
  border-radius: 5px;
  background-color:rgb(212, 187, 212);
  margin-bottom: 10px;
  justify-content: space-between;
}
.app__dropdown{
  background-color: white;
  padding-right: 5px;
}
.app__Statistics{
  display:flex;
  padding:5px;
  background-color:rgb(247, 226, 247);
  justify-content: space-between;
}
.app__firstpart{
  flex: 0.9 1;
}
.app__secondPart {
  display: flex;
  flex-direction: column;
}

.app__graphTitle {
  margin-top: 20px;
  margin-bottom: 20px;
}

.app__right .MuiCardContent-root {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.app__graph {
  flex-grow: 1;
}

@media (max-width: 990px) {
  .app {
    flex-direction: column;
  }
}

.infoBox {
    flex:1 1;    
  }

.infoBox:not(:last-child) {
  margin-right: 10px;
}

.infoBox_cases {
  color: #cc1034;
  font-weight: 600;
  font-size: 1.75rem;
  margin-bottom: 0.5rem;
}

.infoBox--selected {
  border-top: 10px solid greenyellow;
}

.infoBox--red {
  border-color: red;
}

.infoBox_cases--green {
  color: lightgreen !important;
}

.infoBox_cases--grey {
    color: grey !important;
  }


.infoBox--grey {
  border-color:grey;
}


.infoBox_total {
  color: #6c757d;
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  margin-top: 15px !important;
}
.map {
    height: 600px;
    background-color: white;
    padding: 1rem;
    border-radius: 20px;
    margin-top: 16px;
    box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
  }
  
  .map .leaflet-container {
    height: 100%;
  }
  .info-flag img {
    width: 100px;
    border-radius: 5px;
  }
  
  .info-name {
    font-size: 20px;
    font-weight: bold;
    color: #555;
  }
  
  .info-container {
    width: 150px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
  
  .info-flag {
    height: 80px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
  }
  
  .info-confirmed,
  .info-recovered,
  .info-deaths {
    font-size: 16px;
    margin-top: 5px;
  }
  
.table{
    margin-top: 20px;
    overflow: auto;
    height:500px;
    color:gray;
}
.table tr{
    display: flex;
    justify-content: space-between;
}
.table td {
    padding: 0.5rem;
}
.table tr:nth-of-type(odd){
    background-color:#f3f2f8;
}
